<template>
  <div class="custom-upload-img-com">
    <div class="upload-img-com-wrapper" v-show="useType !== 'slot'">
      <el-alert title="åªèƒ½ä¸Šä¼ å°äº 1M çš„å›¾ç‰‡" type="warning" effect="dark" :closable="false" />
      <div class="option">
        <label for="up-to-server">
          <i class="option-item ok el-icon-picture">ä¸Šä¼ æœåŠ¡å™¨
            <input
              id="up-to-server"
              class="img-inp upload"
              type="file"
              title="é€‰æ‹©å›¾ç‰‡"
              accept="image/png, image/jpeg, image/gif, image/jpg"
              @change="uploadServer"
            >
          </i>
        </label>
        <label for="up-to-qiniu">
          <i class="option-item ok el-icon-picture">ä¸Šä¼ ä¸ƒç‰›äº‘
            <input
              id="up-to-qiniu"
              class="img-inp upload"
              type="file"
              title="é€‰æ‹©å›¾ç‰‡"
              accept="image/png, image/jpeg, image/gif, image/jpg"
              @change="uploadQiniu"
            >
          </i>
        </label>
      </div>
    </div>
    <slot v-bind:trigger="customTriggerInp" />
  </div>
</template>

<script>
/**
 * ä¸Šä¼ å›¾ç‰‡ç»„ä»¶ï¼Œæ”¯æŒä¸Šä¼ æ–¹å¼ï¼š1.ä¸ƒç‰›äº‘ï¼Œ2.æœ¬åœ°æœåŠ¡å™¨
 * props: 1.tokenï¼šä¸ƒç‰›äº‘tokenï¼Œ
 * emit: 1.getLocalUrlï¼šè·å–å›¾ç‰‡çš„æœ¬åœ°URL
 */
import { imgRandomName, isProduction, genGuid } from "@/utils";
import { uploadImgStatusMap, server_URL, qiniu_URL } from "@/const";
import { connect } from "tls";
export default {
  props: ["token", "getStatus", "getLocalUrl", "useType"],
  methods: {
    customTriggerInp() {
      // è‡ªå®šä¹‰ä¸Šä¼ å›¾ç‰‡çš„è§¦å‘äº‹ä»¶
      const fileInp = document.querySelector("#up-to-qiniu"); // ä¸ƒç‰›äº‘ä¸Šä¼ 
      fileInp.click(); // è§¦å‘ä¸Šä¼ 
    },
    createObjetURL(file, guid) {
      // åˆ›å»ºå›¾ç‰‡çš„URL
      const url = window.URL.createObjectURL(file); // åˆ›å»ºå›¾ç‰‡çš„URL
      this.getLocalUrl(url, guid); // è·å–å›¾ç‰‡çš„æœ¬åœ°URL
    },
    /**ä¸Šä¼ è‡³ä¸ƒç‰›äº‘ */
    uploadQiniu(e) {
      const IMG_URL = qiniu_URL;
      const guid = genGuid(); // ç”Ÿæˆguid
      const [file] = e.target.files; // è·å–æ–‡ä»¶
      typeof this.getLocalUrl === "function" && this.createObjetURL(file, guid); // åˆ›å»ºå›¾ç‰‡çš„URL
      // return
      const fileType = file.type && file.type.split("/")[1]; // è·å–æ–‡ä»¶ç±»å‹
      const fileSize = file.size / 1024 / 1024; // è·å–æ–‡ä»¶å¤§å°
      if (fileSize > 1) {
        // åˆ¤æ–­æ–‡ä»¶å¤§å°
        this.$message.error("åªèƒ½ä¸Šä¼ å°äº1Mçš„å›¾ç‰‡ï¼æ¢ä¸€ä¸ªå°å›¾ç‰‡è¯•è¯•å§~~");
        return;
      }
      const putExtra = {
        // ä¸Šä¼ å‚æ•°
        fname: "", // æ–‡ä»¶å
        params: "", // å‚æ•°
        mineType: ["image/png", "image/jpeg", "image/gif"] // å…è®¸ä¸Šä¼ çš„æ–‡ä»¶ç±»å‹
      };
      const config = {
        useCdnDomain: true // ä½¿ç”¨ä¸ƒç‰›äº‘çš„CDNåŸŸå
      };
      const error = err => {
        // ä¸Šä¼ å¤±è´¥
        this.getStatus({ status: uploadImgStatusMap.error, data: err, guid }); // è·å–ä¸Šä¼ çŠ¶æ€
      };
      const next = res => {
        // ä¸Šä¼ æˆåŠŸ
        // console.log(res)
        this.getStatus({ status: uploadImgStatusMap.next, data: res, guid }); // è·å–ä¸Šä¼ çŠ¶æ€
      };
      const complete = res => {
        // ä¸Šä¼ å®Œæˆ
        this.getStatus({
          status: uploadImgStatusMap.complete, // ä¸Šä¼ å®Œæˆ
          data: res,
          guid
        });
      };
      const subObject = {
        // ä¸Šä¼ å‚æ•°
        next, // ä¸Šä¼ æˆåŠŸ
        error, // ä¸Šä¼ å¤±è´¥
        complete // ä¸Šä¼ å®Œæˆ
      };
      const imgName = imgRandomName() + "." + fileType; // ç”Ÿæˆå›¾ç‰‡å
      const observable = window.qiniu.upload(
        file, // æ–‡ä»¶
        imgName, // å›¾ç‰‡å
        this.token, // ä¸Šä¼ å‡­è¯
        putExtra, // ä¸Šä¼ å‚æ•°
        config // ä¸Šä¼ é…ç½®
      );
      const subScription = observable.subscribe(subObject); // è®¢é˜…
    },
    /**ä¸Šä¼ è‡³æœ¬åœ°æœåŠ¡å™¨ */
    uploadServer(e) {
      if (isProduction()) {
        // åˆ¤æ–­æ˜¯å¦ä¸ºç”Ÿäº§ç¯å¢ƒ
        return this.$message.error("ä¸ºå‡å°‘æœåŠ¡å™¨å‹åŠ›çº¿ä¸Šè¯·ä¸Šä¼ è‡³ä¸ƒç‰›äº‘å“Ÿ~");
      }
      const IMG_URL = server_URL; // å›¾ç‰‡ä¸Šä¼ åœ°å€
      const guid = genGuid(); // ç”Ÿæˆguid
      const [file] = e.target.files; // è·å–æ–‡ä»¶
      typeof this.getLocalUrl === "function" && this.createObjetURL(file, guid); // åˆ›å»ºå›¾ç‰‡çš„URL
      const fileType = file.type && file.type.split("/")[1]; // è·å–æ–‡ä»¶ç±»å‹
      const fileSize = file.size / 1024 / 1024; // è·å–æ–‡ä»¶å¤§å°
      const formdata = new FormData(); // åˆ›å»ºformdata
      formdata.append("file", file); // æ·»åŠ æ–‡ä»¶
      this.$http.uploadFile(formdata).then(res => {
        // ä¸Šä¼ å›¾ç‰‡
        console.log("ä¸Šä¼ æ–‡ä»¶ç»“æœ", res); // æ‰“å°ç»“æœ
        const { data } = res; // è·å–æ•°æ®
        if (data.status === 2000) {
          // åˆ¤æ–­æ˜¯å¦ä¸Šä¼ æˆåŠŸ
          this.getStatus({
            // è·å–ä¸Šä¼ çŠ¶æ€
            status: uploadImgStatusMap.server_complete,
            data: { key: data.data },
            guid
          });
        } else {
          console.log(res);
        }
      });
    }
  }
};
</script>

<style lang="scss">
.custom-upload-img-com {
  width: 270px;
  background-color: #fff;
  .upload-img-com-wrapper {
    padding: 10px;
    .option {
      margin-top: 10px;
      text-align: right;
      .option-item {
        display: inline-block;
        padding: 5px;
        background-color: #c35673;
        color: #fff;
        height: 40px;
        line-height: 30px;
        text-align: center;
        cursor: pointer;
        border-radius: 5px;
        &::before {
          margin-right: 5px;
        }
      }
      .img-inp {
        display: none;
      }
    }
  }
}
</style>



